.Cover {
  width: 100%;
  height: 100vh;
  background-image: url("bgImage.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
}
.shade {
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.5);
}
.Header {
  width: 80%;
  height: 100px;
  margin: 0 auto;
}
.navBar {
  width: 100%;
  height: 50px;
  margin: 30px auto;
}

.logo {
  font-size: 40px;
  color: #fff;
}

.textFields {
  width: 80%;
  margin: 60px auto;
}
.detailsText {
  color: #fff;
  font-size: 20px;
}
.soonText {
  color: #fff;
  font-size: 30px;
}
.italicStyle {
  font-style: italic;
}